/* src/App.css */

.App {
  text-align: center;
  margin-bottom:100px;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    transition: opacity 0.5s;
    opacity: 0;
    pointer-events: none;
  }
  
  .overlay.visible {
    opacity: 1;
    pointer-events: auto;
  }
  
  .full-screen-image {
    max-width: 90%;
    max-height: 90%;
  }
  
  .close-icon {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
  }
  
  .blurred {
    filter: blur(5px);
    transition: filter 0.5s;
  }
  